import Seo from "components/Seo";
import Layout from "./Layout";
import Header from "components/common/Header";
import DARK_THEME from "lib/theme";
import { Menu, Box, Grommet, Avatar } from "grommet";
import { useFirebase, withFirebaseProvider } from "components/FirebaseProvider";
import { navigate } from "gatsby";
import { useEffect } from "react";
import googleSignIn from "../../services/google-signin";

const UserName = ({ user }) => {
  if (!user) return "loading";
  return (
    <Box direction="row" gap="small" align="center">
      {user.email}
      <Avatar src={user.photoURL} />
    </Box>
  );
};

const DashboardLayout = ({ noScale, children }) => {
  const { firebase, user, isLoading } = useFirebase();

  const logout = () => {
    console.log("navigate", navigate("/"));
    setTimeout(() => firebase.auth().signOut(), 100);
  };

  useEffect(() => {
    if (!firebase) return;
    if (!isLoading) {
      if (!user) {
        googleSignIn(firebase)
          .then(() => navigate("/dashboard/schedule", { replace: true }))
          .catch(err => {
            console.log("catch", err);
            navigate("/", { replace: true });
          });
      }
    }
  }, [firebase, isLoading, user]);

  return (
    <Layout>
      <Seo noScale={noScale} />
      <Grommet plain theme={DARK_THEME} themeMode="dark">
        <Header>
          <Menu
            dropProps={{ animation: "slideDown" }}
            disabled={!user}
            dropAlign={{ top: "top", right: "right" }}
            label={<UserName user={user} />}
            dropBackground={{ color: "brand", opacity: "weak" }}
            items={[{ label: "Logout", onClick: logout }]}
          ></Menu>
        </Header>
        {children}
      </Grommet>
    </Layout>
  );
};

export default DashboardLayout;
