import { createContext, useState, useEffect, useContext } from "react";

const isDev = process.env.GATSBY_NODE_ENV === "development";

export const FirebaseContext = createContext();

export const useFirebase = () => {
  return useContext(FirebaseContext);
};

export const useFunctions = () => {
  const { firebase } = useFirebase();
  if (!firebase) return null;
  return isDev
    ? firebase.functions()
    : firebase.app().functions("europe-west1");
};

const firebaseConfig = {
  apiKey: process.env.GATSBY_FIREBASE_API_KEY,
  authDomain: process.env.GATSBY_FIREBASE_AUTH_DOMAIN,
  projectId: process.env.GATSBY_FIREBASE_PROJECT_ID,
  storageBucket: process.env.GATSBY_FIREBASE_STORAGE_BUCKET,
  messagingSenderId: process.env.GATSBY_FIREBASE_MESSAGING_SENDER_ID,
  appId: process.env.GATSBY_FIREBASE_APP_ID,
  measurementId: process.env.GATSBY_FIREBASE_MEASUREMENT_ID,
};

export const getFirebaseClient = async () => {
  const firebase = (await import("firebase/app")).default;
  if (!firebase.apps || !firebase.apps.length) {
    await Promise.all([import("firebase/auth"), import("firebase/functions")]);

    firebase.initializeApp(firebaseConfig);
    const functions = firebase.functions();
    if (isDev) {
      functions.useEmulator("localhost", 5001);
    }
  }

  return firebase;
};

const FirebaseProvider = props => {
  const [firebase, setFirebase] = useState(null);
  const [isLoading, setLoading] = useState(true);
  const [user, setUser] = useState(null);
  useEffect(() => {
    getFirebaseClient().then(firebase => {
      setFirebase(firebase);
    });
  }, []);

  useEffect(() => {
    if (!firebase) return;
    const unlisten = firebase.auth().onAuthStateChanged(authUser => {
      authUser ? setUser(authUser) : setUser(null);
      setLoading(false);
    });
    return () => {
      unlisten();
    };
  }, [firebase]);

  const value = {
    firebase,
    user,
    isLoading,
  };

  return (
    <FirebaseContext.Provider value={value}>
      {props.children}
    </FirebaseContext.Provider>
  );
};

export const withFirebaseProvider = Component => {
  const WrappedComponent = props => (
    <FirebaseProvider>
      <Component {...props} />
    </FirebaseProvider>
  );

  return WrappedComponent;
};

export default FirebaseProvider;
